$btn-primary:  #A4904F;

.header {
	background-image: url('images/img_poster.jpg');
	color: white;
	padding-bottom: 32px;
	padding-top: 128px; }

.hero-title {
	margin-top: 64px; }

.hero-subtitle {
	margin-bottom: 32px; }

.hero-btn {
	background-color: $btn-primary;
	color: white;

	&:hover {
		background-color: darken($btn-primary, 10);
		color: white; } }

.about-lotr {
	padding-top: 64px;
	padding-bottom: 128px;
	background-color: lighten($btn-primary, 40); }

.author-image img {
	width: 240px; }

@media (max-width: 576px ) {
	.author-image-holder {
		order: -1; } }

